.react-tabs {
    -webkit-tap-highlight-color: transparent;
  }
  
  .react-tabs__tab-list {
    /*border-bottom: 1px solid rgba(0, 0, 0, 0.12);*/
    margin: 0 0 0 10px;
    padding: 0;
  }
  
  .react-tabs__tab {
    display: inline-block;
    /*border: 1px solid transparent;*/
    border-bottom: none;
    bottom: -1px;
    position: relative;
    list-style: none;
    padding: 6px 12px;
    cursor: pointer;
    color: rgb(165, 156, 156);
    font-size: 14px;
  }

  .react-tabs__tab span {
    padding: 4px 0 0 0;
  }
  .react-tabs__tab--selected {
    /*background: #fff;*/
    color: black;
    border-radius: 8px 8px 0 0;
  }

  .react-tabs-dark__tab--selected {
    background: #202940;
    color: white;
    border-radius: 8px 8px 0 0;
  }
  
  .react-tabs__tab--disabled {
    color: GrayText;
    cursor: default;
  }
  
  .react-tabs__tab:focus {
    outline: none;
  }
  
  .react-tabs__tab:focus:after {
    content: '';
    position: absolute;
    height: 5px;
    left: -4px;
    right: -4px;
    bottom: -5px;
    /*background: #fff;*/
  }
  
  .react-tabs__tab-panel {
    display: none;
  }
  
  .react-tabs__tab-panel--selected {
    display: block;
  }